"use client";

import { useState, useEffect } from "react";

interface UserProfile {
  full_name?: string;
  age?: number;
  weight?: number;
  height?: number;
  gender?: "male" | "female";
  activity_level?:
    | "sedentary"
    | "light"
    | "moderate"
    | "active"
    | "very_active";
  goal?: "lose" | "maintain" | "gain";
  daily_calorie_goal?: number;
}

interface ProfileSetupProps {
  userProfile?: UserProfile;
  onProfileUpdate: (profile: UserProfile) => void;
  onClose?: () => void;
  embedded?: boolean;
}

export default function ProfileSetup({
  userProfile,
  onProfileUpdate,
  onClose,
  embedded = false,
}: ProfileSetupProps) {
  const [formData, setFormData] = useState({
    full_name: "",
    age: "",
    weight: "",
    height: "",
    gender: "male" as "male" | "female",
    activity_level: "moderate" as
      | "sedentary"
      | "light"
      | "moderate"
      | "active"
      | "very_active",
    goal: "maintain" as "lose" | "maintain" | "gain",
  });
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState("");

  useEffect(() => {
    if (userProfile) {
      setFormData({
        full_name: userProfile.full_name || "",
        age: userProfile.age?.toString() || "",
        weight: userProfile.weight?.toString() || "",
        height: userProfile.height?.toString() || "",
        gender: userProfile.gender || "male",
        activity_level: userProfile.activity_level || "moderate",
        goal: userProfile.goal || "maintain",
      });
    }
  }, [userProfile]);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);
    setError("");

    try {
      const response = await fetch("/api/profile", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          ...formData,
          age: parseInt(formData.age),
          weight: parseFloat(formData.weight),
          height: parseInt(formData.height),
        }),
      });

      const data = await response.json();

      if (data.success) {
        onProfileUpdate(data.profile);
        if (onClose) onClose();
      } else {
        setError(data.error || "A apărut o eroare");
      }
    } catch {
      setError("A apărut o eroare la salvarea profilului");
    } finally {
      setLoading(false);
    }
  };

  const activityLevels = [
    {
      value: "sedentary",
      label: "Sedentar",
      description: "Puțină sau deloc activitate fizică",
    },
    {
      value: "light",
      label: "Ușor activ",
      description: "Exerciții ușoare 1-3 zile/săptămână",
    },
    {
      value: "moderate",
      label: "Moderat activ",
      description: "Exerciții moderate 3-5 zile/săptămână",
    },
    {
      value: "active",
      label: "Activ",
      description: "Exerciții intense 6-7 zile/săptămână",
    },
    {
      value: "very_active",
      label: "Foarte activ",
      description: "Exerciții foarte intense, 2x pe zi",
    },
  ];

  const goals = [
    {
      value: "lose",
      label: "Pierdere în greutate",
      description: "Vreau să slăbesc",
    },
    {
      value: "maintain",
      label: "Menținere",
      description: "Vreau să îmi mențin greutatea",
    },
    {
      value: "gain",
      label: "Creștere în greutate",
      description: "Vreau să mă îngraș/să câștig masă musculară",
    },
  ];

  const content = (
    <div className="space-y-6">
      <div className="text-center">
        <h2 className="text-2xl font-bold text-gray-800 mb-2">
          {userProfile
            ? "Actualizează-ți profilul"
            : "Să te cunoaștem mai bine"}
        </h2>
        <p className="text-gray-600">
          Aceste informații ne ajută să îți calculăm necesarul caloric și să îți
          oferim recomandări personalizate
        </p>
      </div>

      <form onSubmit={handleSubmit} className="space-y-4">
        <div>
          <label className="block text-sm font-medium text-gray-700 mb-1">
            Numele complet
          </label>
          <input
            type="text"
            value={formData.full_name}
            onChange={(e) =>
              setFormData({ ...formData, full_name: e.target.value })
            }
            className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
            placeholder="Alex Popescu"
            required
          />
        </div>

        <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">
              Vârsta (ani)
            </label>
            <input
              type="number"
              value={formData.age}
              onChange={(e) =>
                setFormData({ ...formData, age: e.target.value })
              }
              className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
              placeholder="25"
              min="13"
              max="100"
              required
            />
          </div>

          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">
              Greutatea (kg)
            </label>
            <input
              type="number"
              step="0.1"
              value={formData.weight}
              onChange={(e) =>
                setFormData({ ...formData, weight: e.target.value })
              }
              className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
              placeholder="70"
              min="30"
              max="300"
              required
            />
          </div>

          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">
              Înălțimea (cm)
            </label>
            <input
              type="number"
              value={formData.height}
              onChange={(e) =>
                setFormData({ ...formData, height: e.target.value })
              }
              className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
              placeholder="175"
              min="120"
              max="250"
              required
            />
          </div>
        </div>

        <div>
          <label className="block text-sm font-medium text-gray-700 mb-3">
            Sexul
          </label>
          <div className="flex space-x-4">
            <label className="flex items-center">
              <input
                type="radio"
                value="male"
                checked={formData.gender === "male"}
                onChange={(e) =>
                  setFormData({
                    ...formData,
                    gender: e.target.value as "male" | "female",
                  })
                }
                className="mr-2"
              />
              <span>Masculin</span>
            </label>
            <label className="flex items-center">
              <input
                type="radio"
                value="female"
                checked={formData.gender === "female"}
                onChange={(e) =>
                  setFormData({
                    ...formData,
                    gender: e.target.value as "male" | "female",
                  })
                }
                className="mr-2"
              />
              <span>Feminin</span>
            </label>
          </div>
        </div>

        <div>
          <label className="block text-sm font-medium text-gray-700 mb-3">
            Nivelul de activitate
          </label>
          <div className="space-y-2">
            {activityLevels.map((level) => (
              <label key={level.value} className="flex items-start">
                <input
                  type="radio"
                  value={level.value}
                  checked={formData.activity_level === level.value}
                  onChange={(e) =>
                    setFormData({
                      ...formData,
                      activity_level: e.target
                        .value as typeof formData.activity_level,
                    })
                  }
                  className="mr-2 mt-1"
                />
                <div>
                  <div className="font-medium">{level.label}</div>
                  <div className="text-sm text-gray-600">
                    {level.description}
                  </div>
                </div>
              </label>
            ))}
          </div>
        </div>

        <div>
          <label className="block text-sm font-medium text-gray-700 mb-3">
            Obiectivul tău
          </label>
          <div className="space-y-2">
            {goals.map((goal) => (
              <label key={goal.value} className="flex items-start">
                <input
                  type="radio"
                  value={goal.value}
                  checked={formData.goal === goal.value}
                  onChange={(e) =>
                    setFormData({
                      ...formData,
                      goal: e.target.value as typeof formData.goal,
                    })
                  }
                  className="mr-2 mt-1"
                />
                <div>
                  <div className="font-medium">{goal.label}</div>
                  <div className="text-sm text-gray-600">
                    {goal.description}
                  </div>
                </div>
              </label>
            ))}
          </div>
        </div>

        {error && (
          <div className="bg-red-50 border border-red-200 rounded-md p-3">
            <p className="text-sm text-red-600">{error}</p>
          </div>
        )}

        <button
          type="submit"
          disabled={loading}
          className="w-full bg-blue-600 text-white py-2 px-4 rounded-md hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-blue-500 disabled:opacity-50 disabled:cursor-not-allowed"
        >
          {loading ? (
            <div className="flex items-center justify-center">
              <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white mr-2"></div>
              Se salvează...
            </div>
          ) : userProfile ? (
            "Actualizează profilul"
          ) : (
            "Salvează profilul"
          )}
        </button>

        {onClose && !embedded && (
          <button
            type="button"
            onClick={onClose}
            className="w-full bg-gray-200 text-gray-800 py-2 px-4 rounded-md hover:bg-gray-300 focus:outline-none focus:ring-2 focus:ring-gray-500"
          >
            Anulează
          </button>
        )}
      </form>
    </div>
  );

  if (embedded) {
    return <div className="max-w-2xl mx-auto">{content}</div>;
  }

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4">
      <div className="bg-white rounded-lg shadow-xl max-w-2xl w-full max-h-[90vh] overflow-y-auto p-6">
        {content}
      </div>
    </div>
  );
}
